    font-family: 'Barlow', sans-serif;
}

/**********************/
a{
    text-decoration: none !important;
}
ul{
    padding: 0;
    margin: 0;
}
ul li{
    list-style-type: none !important;
}
h1,h2,h3,h4,h5,h6,p{
    margin: 0px;
    padding: 0px;
}


/********** ACTIVE CLASS************/
.active_class{
    background: #FF284F;
    color: #fff !important;
}



/************** FOOTER*****************/
#footer{
    background: #2C2B37;
    padding: 50px 30px;
    color: rgb(199, 199, 199);
}
#footer .foot_logo{
    margin-bottom: 30px;
}
#footer .foot_social{
    margin-bottom: 30px;
}
#footer .foot_social ul{
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
}
#footer .foot_social ul li{
    list-style-type: none;
    margin-right: 10px;
}
#footer .foot_social ul li .social_icon{
    color: #fff;
    font-size: 20px;
    margin-right: 25px;
}
#footer .foot_text p{
    font-size: 15px;
    color: #fff;
    margin-bottom: 20px;
}
#footer h4{
    font-size: 18px;
    font-weight: 600;
    margin-bottom: 20px;
    color: rgb(235, 235, 235);
}
#footer p{
    margin-bottom: 5px;
    font-size: 16px;
    font-weight: 500;
    transition: .5s;
}
#footer .foot_link{
    color: rgb(199, 199, 199);
}
#footer .foot_link:hover{
    color: #fff;
}


#footer .footer_bottom{
    border-top: 2px solid #616161;
    margin-top: 30px;
}
#footer .footer_bottom p{
    color: #fff;
    font-size: 14px;
    margin-top: 30px;
}


#study{
   background: #ebebeb;
   padding: 40px 0px;
}
#study p{
    margin-bottom: 0px !important;
    font-size: 15px;
    color: rgb(82, 82, 82);
    margin-right: 20px;
}
.study_top{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
}
#study .study_top .study_left ul{
    display: flex;
    flex-direction: row;
    justify-content: center;
    background: #F4F4F4;
    padding: 12px 20px;
    border-radius: 20px;
}
#study .study_top .study_left ul li .study_left_link{
   padding: 5px 20px;
   font-size: 16px;
   font-weight: 500;
   color: #000;
   border-radius: 20px;
   transition: .5s;
   margin: 0px 5px;
}
#study .study_top .study_left ul li .study_left_link:hover{
    background: #FF284F;
    color: #fff;
}
#study .study_top .study_right{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    height: 100%;
}
#study .study_top .study_right .drop_btn{
    border: none;
    background: #FF284F;
    color: #fff;
    padding: 10px;
    border-radius: 8px;
    font-size: 15px;
}


#study .study_top .study_right .dropdown-menu {
    background: #ffffff;
    display: none;
    position: absolute;
    border: 1px solid #c2c2c2;
    overflow-y: scroll;
    height: 350px;
  }
  
  #study .study_top .study_right .dropdown:hover .dropdown-menu {
      padding: 10px 0px;
    display: block;
    transition: all .5s ease-in-out;
  }
  #study .study_top .study_right .dropdown-menu .class_drop_btn{
      font-size: 14px;
      font-weight: 600;
  }
  #study .study_top .study_right .dropdown-menu img{
      width: 30px;
      margin: 3px 10px 3px 0px;
  }
/**************STUDY CARD***********/
#study .study_card{
    padding-top: 30px;
}
#study .study_card .card_parent .card_title{
    margin-bottom: 10px;
}
#study .study_card h4{
    font-weight: 600;
    font-size: 25px;
    color: rgb(46, 46, 46);
    line-height: 27px;
    margin-bottom: 0px;
    padding-bottom: 0px;
}
#study .study_card .card_parent{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin: 0 auto;
    align-items: center;
    justify-content: flex-start;
}
#study .study_card .card_items{
    display: flex;
    flex-direction: row;
    align-items: center;
    background: #fff;
    padding: 10px 10px;
    width: 258px;
    border-radius: 15px;
    margin: 10px;
    transition: .6s;
}
#study .study_card .card_items:hover{ 
    box-shadow: 2px 2px 9px 4px rgba(145,145,145,0.32);
}
#study .study_card .card_items img{
    width: 50px;
}
#study .study_card .card_items h6{
    color: #000;
    font-size: 15px;
    line-height: 18px;
    font-weight: 600;
    padding-left: 7px;
    margin-bottom: 0px !important;
}


/************ Study Video ***************/
.study_video{
    background: #EBEBEB;
    padding: 20px 0px;
    height: 100vh;
}
.s_video iframe{
    border-radius: 15px;
}
.s_title_list{
    background: #fff;
    padding: 10px;
    border-radius: 10px;
}
.s_title_list ul{
    height: 550px;
    overflow-y: scroll;
    background: #f0f0f0;
    margin: 0px;
    padding: 0px;
}
.s_title_list ul li{
    padding: 10px 10px;
    margin: 10px 10px;
    background: #fff;
    border-radius: 5px;
    cursor: pointer;
}
.s_title_list ul li .s_link{
    color: rgb(68, 68, 68);
    font-size: 14px;
    font-weight: 700;
    cursor: pointer;
}

#skill{
    background: #f3f3f3;
    padding: 30px 0px;
}
#skill .skill_heading h4{
    font-size: 20px;
    font-weight: 700;
    line-height: 22px;
    color: #3f3f3f;
    margin-bottom: 10px;
    margin-top: 30px;
}
 .skill_body{
    width: 100%;
    height: 100%;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: flex-start;
    align-items: flex-start;
}
 .skill_item{
    margin: 7px 7px 15px 7px;
    background: #fff;
    padding: 13px;
    border-radius: 10px;
    width: 265px;
    transition: .5s;
    display: inline-block;
}
 .skill_item:hover{ 
    box-shadow: 2px 2px 9px 4px rgba(145,145,145,0.32);
}
 .skill_item img{
    width: 232px;
    border-radius: 10px;
}
 .skill_item .skill_title h6{
    font-weight: 700;
    font-size: 16px;
    line-height: 18px;
    margin-top: 12px;
    color: #303030;
    word-wrap: break-word;
    overflow: hidden;
    display: -webkit-box;
    text-overflow: ellipsis;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
}
 .skill_item .skill_price_time{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 10px 0px;
}
.skill_item .skill_price_time h5{
    color: #45C885;
    font-size: 16px;
    font-weight: 700;
    margin: 0px;
}
 .skill_item .skill_price_time small{
    background: #f7dbdf;
    color: crimson;
    font-size: 12px;
    padding: 3px 5px;
    font-weight: 600;
    border-radius: 10px;
}


/************ Skill Slider ****************/
.slider_title h4{
    margin-top: 30px;
    font-size: 22px;
    font-weight: bold;
    color: #303030;
}
.skill_slider{
   width: 100%;
   height: 100%;
   margin-top: 50px;
}
.skill_sli_item{
    width: 100%;
    height: 300px;
    display: flex;
    flex-direction: column;
    background: #fff;
    padding: 20px 20px;
    border-radius: 20px;
    
}
.skill_sli_item img{
    width: 50px;
    height: 50px;
    border-radius: 50%;
    margin-right: 20px;
}
.skill_sli_item .top_img{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    align-items: center;
    margin-bottom: 15px;
}
.skill_sli_item h6{
    font-size: 16px;
    font-weight: 700;
    color: rgb(26, 26, 26);
    margin-bottom: 3px;
}
.skill_sli_item small{
    font-size: 13px;
    color: rgb(88, 88, 88);
    margin-bottom: 3px;
}

.skill_sli_item .skill_slid_text p{
    font-size: 15px;
    color: rgb(88, 88, 88);
}

.skill_slider .slick-prev:before, .skill_slider .slick-next:before {
    font-size: 35px !important;
     color: rgb(68, 68, 68) !important; 
 }
 .skill_slider .slick-prev {
    left: 40% !important;
}
.skill_slider .slick-next {
    right: 40% !important;
}
.skill_slider .slick-prev, .skill_slider .slick-next {
    margin-top: 5px !important;
    font-size: 0;
    line-height: 0;
    position: absolute;
    top: 110% !important;
    display: block;
    width: 40px !important;
    height: 40px !important;
    padding: 0;
    transform: translate(0, -50%);
    cursor: pointer;
    color: transparent;
    border: none;
    outline: none;
    background: transparent;
}
.question{
    margin-top: 80px;
}
.question h4{
    margin-top: 30px;
    font-size: 22px;
    font-weight: bold;
    color: #303030;
