.book{
    width: 100%;
    height: 100%;
    background: #f7f7f7;
    padding: 30px 0px 70px 0px;
}
.book .over_all{
    background: #fff;
    padding: 20px;
    margin-top: 10px;
    border-radius: 10px;
}
.book_heading{
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
}
.book_heading h3{
    font-size: 25px;
    font-weight: 700;
    color: rgb(68, 68, 68);
}
.book_heading .book_btn_icon{
    color: rgb(211, 211, 211);
    padding: 7px 15px;
    font-size: 13px;
    background: rgb(56, 56, 56);
    transition: .5s;
    border: 1px solid rgb(56, 56, 56);
}
.book_heading .book_btn_icon:hover{
    background: #fff;
    color: rgb(68, 68, 68);
    border: 1px solid rgb(56, 56, 56);
}
.book_cards{
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-around;
    align-items: center;
}
.book_cards .item{
    background: #ececec;
    padding: 30px 30px;
    border-radius: 10px;
    width: 235px;
    margin: 10px 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
}
.book_cards .item .price{
    position: absolute;
    top: 5px;
    right: 15px;
}
.book_cards .item .price p{
    background: #45C881;
    font-size: 13px;
    font-weight: 600;
    color: #fff;
    padding: 2px 5px;
    border-radius: 10px;
}
.book_cards .item .book_text{
    margin-top: 15px;
    align-items: center;
    text-align: center;
}
.book_cards .item h5{
    margin-bottom: 0px;
    color: rgb(68, 68, 68);
    word-wrap: break-word;
    overflow: hidden;
    display: -webkit-box;
    text-overflow: ellipsis;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 1;
    font-size: 15px;
    font-weight: 600;
}
.book_cards .item small{
    color: rgb(68, 68, 68);
    font-size: 15px;
}
.book_cards img{
    width: 150px;
    border-radius: 10px;
    margin: 0 auto;
}
